  background-color: #fafafa;
  color: #0ab89d;
  padding: 10px 20px 10px 20px;
  border-radius: 25px;
  border: 2px solid #fafafa;
}
.btn--doted {
  color: #191919;
  position: relative;
  display: inline-block;
}
.btn--doted:after {
  content: "";
